"use client";

import { useState, useEffect, useMemo } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useParams, useRouter } from "next/navigation";
import { fetchTaskById, updateTask } from "@/features/taskSlice";
import { fetchTeamByProjectId } from "@/features/teamSlice";
import { ArrowLeft, FileText, User, Flag, CalendarIcon, X, Loader, Command } from "lucide-react";
import { toast } from "sonner";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Calendar } from "@/components/ui/calendar";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { format } from "date-fns";
import { cn } from "@/lib/utils";
import { CommandEmpty, CommandGroup, CommandInput, CommandItem } from "cmdk";

const EditTask = ({ taskId }) => {
  const dispatch = useDispatch();
  const router = useRouter();
  const params = useParams();
  const task_id = taskId || params.id; // Fallback to params.id if taskId prop is not provided
  const task = useSelector((state) => state.task?.currentTask);
  const { teamsByProject: teams, status: teamStatus } = useSelector((state) => state.team || {});
  const taskStatus = useSelector((state) => state.task?.status);
  const error = useSelector((state) => state.task?.error);

  const [formData, setFormData] = useState({
    title: "",
    description: "",
    assignedTo: "",
    assignedBy: "",
    priority: "Low",
    deadline: "",
    projectId: "",
    projectName: "",
    teamId: "",
    memberId: "",
  });

  const [selectedTeam, setSelectedTeam] = useState(null);
  const [selectedDate, setSelectedDate] = useState(null);
  const [memberSearchQuery, setMemberSearchQuery] = useState("");
  const [memberOpen, setMemberOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (!task_id) {
      toast.error("Invalid task ID");
      router.back();
      return;
    }
    setIsLoading(true);
    dispatch(fetchTaskById(task_id))
      .unwrap()
      .then(() => setIsLoading(false))
      .catch(() => {
        toast.error("Failed to fetch task");
        setIsLoading(false);
      });
  }, [dispatch, task_id, router]);

  useEffect(() => {
    if (task) {
      const formattedDeadline = task.deadline
        ? new Date(task.deadline).toISOString().slice(0, 10)
        : "";
      setFormData({
        title: task.title || "",
        description: task.description || "",
        assignedTo: task.assignedTo || "",
        assignedBy: task.assignedBy || "",
        priority: task.priority || "Low",
        deadline: formattedDeadline,
        projectId: task.projectId || "",
        projectName: task.projectName || "",
        teamId: task.teamId || "",
        memberId: task.memberId || "",
      });
      setSelectedDate(task.deadline ? new Date(task.deadline) : null);
      if (task.projectId) {
        dispatch(fetchTeamByProjectId(task.projectId));
      }
    }
  }, [task, dispatch]);

  useEffect(() => {
    if (selectedTeam) {
      setFormData((prev) => ({
        ...prev,
        assignedBy: selectedTeam.teamLeadName || prev.assignedBy,
        projectId: selectedTeam.projectId || prev.projectId,
        projectName: selectedTeam.projectName || prev.projectName,
        teamId: selectedTeam.teamId || prev.teamId,
        assignedTo: "",
        memberId: "",
      }));
      setMemberSearchQuery("");
    }
  }, [selectedTeam]);

  const teamOptions = useMemo(() => {
    return teams?.map((team) => ({
      value: team.teamId,
      label: team.teamName,
    })) || [];
  }, [teams]);

  const teamMemberOptions = useMemo(() => {
    if (!selectedTeam?.teamMembers || !Array.isArray(selectedTeam.teamMembers)) {
      return [];
    }
    const query = memberSearchQuery.toLowerCase();
    return selectedTeam.teamMembers
      .filter((member) => member.memberName.toLowerCase().includes(query))
      .map((member) => ({
        value: member.memberId,
        label: member.memberName,
        memberId: member.memberId,
      }));
  }, [selectedTeam, memberSearchQuery]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSelectChange = (name, value) => {
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleMemberSelect = (value) => {
    const member = teamMemberOptions.find((opt) => opt.value === value);
    handleSelectChange("assignedTo", member?.label || "");
    handleSelectChange("memberId", member?.memberId || "");
    setMemberOpen(false);
  };

  const validateForm = () => {
    const requiredFields = ["title", "assignedTo", "assignedBy", "projectId", "teamId"];
    const missingFields = requiredFields.filter((field) => !formData[field]);
    if (missingFields.length > 0) {
      toast.error(`Please fill in all required fields: ${missingFields.join(", ")}`);
      return false;
    }
    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validateForm()) {
      return;
    }
    try {
      const updatedTask = {
        ...formData,
        task_id,
        projectId: selectedTeam?.projectId || formData.projectId,
        projectName: selectedTeam?.projectName || formData.projectName,
        teamId: selectedTeam?.teamId || formData.teamId,
        memberId: formData.memberId,
        deadline: selectedDate ? format(selectedDate, "yyyy-MM-dd") : "",
      };
      await dispatch(updateTask(updatedTask)).unwrap();
      toast.success("Task updated successfully");
      router.back();
    } catch (error) {
      toast.error("Error while updating task");
    }
  };

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gray-100 flex items-center justify-center">
        <Loader className="h-8 w-8 animate-spin text-blue-600" />
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-100 flex items-center justify-center">
        <Card className="border-none shadow-lg bg-white rounded-lg p-6">
          <p className="text-red-600">Error: {error}</p>
          <Button
            onClick={() => router.back()}
            className="mt-4 bg-blue-600 text-white hover:bg-blue-700 rounded-lg"
          >
            Go Back
          </Button>
        </Card>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-100 p-4 sm:p-6 lg:p-8">
      <div className="mx-auto max-w-7xl">
        <Card className="border-none shadow-lg bg-white rounded-lg">
          <CardHeader className="bg-blue-50 px-4 py-3 sm:px-6 sm:py-4 border-b border-gray-200">
            <div className="flex items-center justify-between flex-wrap gap-4">
              <div className="flex items-center gap-3">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => router.back()}
                  className="border-gray-300 bg-white text-gray-700 hover:bg-gray-100 rounded-lg"
                >
                  <ArrowLeft className="h-5 w-5 mr-2" />
                  <span className="hidden sm:inline">Back</span>
                </Button>
                <CardTitle className="text-xl sm:text-2xl font-bold text-gray-800">
                  Edit Task
                </CardTitle>
              </div>
            </div>
          </CardHeader>
          <CardContent className="p-4 sm:p-6">
            <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-[30%_70%] gap-6">
              {/* Left Column (30%) */}
              <div className="space-y-6">
                {/* Task Title */}
                <div>
                  <label className="flex items-center text-sm font-medium text-gray-700 mb-2">
                    <FileText className="h-4 w-4 text-blue-500 mr-2" />
                    Task Title
                  </label>
                  <Input
                    id="title"
                    name="title"
                    value={formData.title}
                    onChange={handleChange}
                    className="w-full bg-white border border-gray-300 rounded-lg text-sm sm:text-base focus:ring-2 focus:ring-blue-300 focus:border-blue-500 py-2 px-3"
                    required
                  />
                </div>

                {/* Priority */}
                <div>
                  <label className="flex items-center text-sm font-medium text-gray-700 mb-2">
                    <Flag className="h-4 w-4 text-blue-500 mr-2" />
                    Priority
                  </label>
                  <Select
                    value={formData.priority}
                    onValueChange={(value) => handleSelectChange("priority", value)}
                  >
                    <SelectTrigger className="w-full bg-white border border-gray-300 rounded-lg text-sm sm:text-base focus:ring-2 focus:ring-blue-300 focus:border-blue-500 py-2 px-3">
                      <SelectValue placeholder="Select priority" />
                    </SelectTrigger>
                    <SelectContent className="bg-white shadow-lg border border-gray-200 rounded-lg text-gray-800 max-h-60">
                      <SelectItem value="Low">Low</SelectItem>
                      <SelectItem value="Medium">Medium</SelectItem>
                      <SelectItem value="High">High</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                {/* Deadline */}
                <div>
                  <label className="flex items-center text-sm font-medium text-gray-700 mb-2">
                    <CalendarIcon className="h-4 w-4 text-blue-500 mr-2" />
                    Deadline
                  </label>
                  <Popover>
                    <PopoverTrigger asChild>
                      <Button
                        variant="outline"
                        className={cn(
                          "w-full justify-between bg-white border border-gray-300 rounded-lg text-sm sm:text-base hover:bg-gray-50 focus:ring-2 focus:ring-blue-300 focus:border-blue-500 py-2 px-3",
                          !selectedDate && "text-gray-500"
                        )}
                      >
                        {selectedDate ? format(selectedDate, "PPP") : "Select date"}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="bg-white border border-gray-200 rounded-lg shadow-lg p-0 w-auto">
                      <Calendar
                        mode="single"
                        selected={selectedDate}
                        onSelect={(date) => {
                          setSelectedDate(date);
                          handleSelectChange("deadline", date ? format(date, "yyyy-MM-dd") : "");
                        }}
                        initialFocus
                        className="rounded-lg text-gray-800"
                      />
                    </PopoverContent>
                  </Popover>
                </div>

                {/* Team Selection */}
                <div>
                  <label className="flex items-center text-sm font-medium text-gray-700 mb-2">
                    <User className="h-4 w-4 text-blue-500 mr-2" />
                    Team
                  </label>
                  <Select
                    value={selectedTeam?.teamId || formData.teamId}
                    onValueChange={(value) => {
                      const team = teams.find((t) => t.teamId === value);
                      setSelectedTeam(team);
                      handleSelectChange("teamId", value);
                    }}
                  >
                    <SelectTrigger className="w-full bg-white border border-gray-300 rounded-lg text-sm sm:text-base focus:ring-2 focus:ring-blue-300 focus:border-blue-500 py-2 px-3">
                      <SelectValue placeholder="Select team" />
                    </SelectTrigger>
                    <SelectContent className="bg-white shadow-lg border border-gray-200 rounded-lg text-gray-800 max-h-60 overflow-y-auto">
                      {teamOptions.map((option) => (
                        <SelectItem key={option.value} value={option.value}>
                          {option.label}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                {/* Assigned To */}
                <div>
                  <label className="flex items-center text-sm font-medium text-gray-700 mb-2">
                    <User className="h-4 w-4 text-blue-500 mr-2" />
                    Assigned To
                  </label>
                  <Popover open={memberOpen} onOpenChange={setMemberOpen}>
                    <PopoverTrigger asChild>
                      <Button
                        variant="outline"
                        className="w-full justify-between bg-white border border-gray-300 rounded-lg text-sm sm:text-base hover:bg-gray-50 focus:ring-2 focus:ring-blue-300 focus:border-blue-500 py-2 px-3"
                        disabled={!selectedTeam}
                        onClick={() => setMemberOpen(true)}
                      >
                        {formData.assignedTo || "Search and select team member"}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="bg-white border border-gray-200 rounded-lg shadow-lg p-0 w-full max-h-60 overflow-y-auto">
                      <Command>
                        <CommandInput
                          placeholder="Search team members..."
                          value={memberSearchQuery}
                          onValueChange={setMemberSearchQuery}
                          className="h-10 text-sm p-2 border-b border-gray-200"
                        />
                        <CommandEmpty>No members found.</CommandEmpty>
                        <CommandGroup>
                          {teamMemberOptions.map((option) => (
                            <CommandItem
                              key={option.value}
                              value={option.value}
                              onSelect={() => handleMemberSelect(option.value)}
                              className={cn(
                                "cursor-pointer text-sm",
                                formData.assignedTo === option.label ? "bg-blue-100 text-blue-800" : "hover:bg-gray-50"
                              )}
                            >
                              {option.label}
                            </CommandItem>
                          ))}
                        </CommandGroup>
                      </Command>
                    </PopoverContent>
                  </Popover>
                </div>
              </div>

              {/* Right Column (70%) */}
              <div className="flex flex-col h-full">
                <label className="flex items-center text-sm font-medium text-gray-700 mb-2">
                  <FileText className="h-4 w-4 text-blue-500 mr-2" />
                  Description
                </label>
                <Textarea
                  id="description"
                  name="description"
                  value={formData.description}
                  onChange={handleChange}
                  placeholder="Enter task description"
                  className="flex-1 bg-white border border-gray-300 rounded-lg text-sm sm:text-base resize-y min-h-[150px] max-h-[500px] p-3 focus:ring-2 focus:ring-blue-300 focus:border-blue-500"
                  required
                />
              </div>

              {/* Form Actions */}
              <div className="md:col-span-2 flex justify-end gap-4 pt-6 border-t border-gray-200">
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => router.back()}
                  className="bg-white text-gray-700 border border-gray-300 hover:bg-gray-50 rounded-lg text-sm sm:text-base px-4 py-2"
                >
                  Cancel
                </Button>
                <Button
                  type="submit"
                  disabled={taskStatus === "loading"}
                  className="bg-blue-600 text-white hover:bg-blue-700 rounded-lg text-sm sm:text-base px-4 py-2 flex items-center"
                >
                  {taskStatus === "loading" ? (
                    <>
                      <Loader className="h-4 w-4 animate-spin mr-2" />
                      Updating...
                    </>
                  ) : (
                    "Update Task"
                  )}
                </Button>
              </div>
            </form>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default EditTask;
